#main {
  display: grid;
  grid-template-columns: auto 350px;
  background-color: #0f1e2c;
  height: 100vh;
}
#preview {
  width: 100%;
  height: 100%;
  border: none;
}
nav {
  max-height: 100vh;
  overflow: scroll;
  display: grid;
  grid-template-rows: 75px auto 75px;
  color: #fff;
}
#badges, #crawler {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
#badges h4 {
  line-height: 40px;
  width: 40px;
  text-align: center;
  background-color: red;
  border-radius: 50%;
  margin-right: 15px;
  transition:
    color .2s ease,
    background-color .2s ease;
}
#badges h4:hover {
  background-color: rgb(0, 174, 91);
  cursor: pointer;
}

.category {
  overflow: scroll;
}
table {
  width: 100%;
}
caption {
  line-height: 40px;
}
td {
  line-height: 30px;
  color: rgba(255,255,255,.5);
  border-bottom: solid 1px rgba(255,255,255,.1);
  cursor: pointer;
  padding-left: 5px;
}
td:hover, .checked {
  color: #fff;
  background: rgba(0,0,0,.2);
}
.checked {
  box-shadow: rgb(0, 174, 91) 3px 0px 0px 0px inset;
}
td input {
  margin: 0 10px;
}
.shade {
  opacity: 0.25;
}
button {
  line-height: 40px;
  width: 123px;
  border: solid 1px #00ae5b;
  background-color: transparent;
  color: #00ae5b;
  font-size: 1em;
  outline: none !important;
  transition:
    background-color .2s ease,
    color .2s ease;
}
button:hover {
  background-color: #00ae5b;
  color: #fff;
  cursor: pointer;
}
#map {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0,0,0,0.75);
}
#map div {
  position: relative;
  background: #fff;
  color: #313131;
  min-width: 300px;
  height: 50%;
  border-radius: 4px;
}
#map input {
  line-height: 40px;
  width: stretch;
  font-size: 1.2em;
  border: none;
  outline: none;
  border-bottom: solid 1px #00ae5b;
  color: #00ae5b;
  padding: 0 0 0 15px;
}
#map ul {
  position: relative;
  max-height: calc(100% - 41px);
  overflow: scroll;
  margin: 0;
  padding: 0;
  list-style: none;
}
#map li {
  line-height: 30px;
  padding-left: 15px;
  border-bottom: 1px solid #eee;
  display: flex;
}
#map li:hover {
  background-color: #f8f8f8;
  cursor: pointer;
}
#close-map {
  position: absolute;
  top: 0;
  right: -50px;
  font-size: 1.3em;
  line-height: 40px;
  width: 40px;
  text-align: center;
  color: #00ae5b;
  border-radius: 4px;
  cursor: pointer;
  transition:
    color .2s ease,
    background-color .2s ease;
}
#close-map:hover {
  color: #fff;
  background-color: #00ae5b;
}
.map-count {
  color: #fff;
  background-color: red;
  text-align: center;
  border-radius: 50%;
  width: 30px;
  margin-right: 15px;
}
